import {instance, APIResponseType, ResultCodesForCaptchaEnum} from "./api";

type MeResponseDataType = {
    id: number
    email: string
    login: string
}
type LoginResponseType = {
    userId: number
}

export const authAPI = {
    me() {
        return instance.get<APIResponseType<MeResponseDataType>>('auth/me').then(response => response.data)
    },
    login(email: string, password: string, rememberMe = false, captcha: null | string = null) {
        return instance.post<APIResponseType<LoginResponseType, ResultCodesForCaptchaEnum>>('auth/login', {
            email, password, rememberMe, captcha
        }).then(response => response.data)
    },
    logout() {
        return instance.delete<APIResponseType>('auth/login')
    }
}